.ServicesSection {
    cursor: pointer;
    margin: 5rem 0;
    width: 18rem;
    align-items: center;
    @media screen and (max-width: 1400px){
        width: 100%;
        margin: 1rem;
    }
    @media screen and (min-width: 1700px){
        width: 22rem;
    }
    .service{
        width: 95%;
        height: 38vh;
        background-color:#191d2b;
        border-left: 1px solid #2e344e;
        border-right: 1px solid #2e344e;
        border-bottom: 1px solid #2e344e;
        border-top: 8px solid #2e344e;
        transition: all .4s ease-in-out;
        &:hover{
            border-top: 8px solid #189ACE;
        }
        @media screen and (max-width: 1400px){
            width: 100%;
        }
        @media screen and (max-width: 541px){
            width: 100%;
            height: 100%;
        }
    }
    .service-content{
        padding: 1rem;
        .service-image{
            width: 4rem;
            height: 4rem;
            opacity: .6;
        }
        .service-title{
            font-size: 1.4rem;
            font-weight: 500;
            position: relative;
            padding-bottom: 1rem;
            margin: 1rem 0;
            &::after{
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                width: 30%;
                height: 1px;
                background-color: #2e344e;
            }
        }
        .service-text{
            font-size: 1.1rem;
        }
        &:hover{
            .service-image{
                opacity: 1;
            }
            .service-title::after{
                background-color: #fff;
            }
        }
    }
}